import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { Homepage, TOC, Tools, Books } from "./Pages/index"
import { NavBar, Foot } from "../src/Components/index"

function App() {
  const [route, changeRoute] = useState("home");
  const home = route === "home";
  const lessonPlans = route === "lessonPlans";
  const tools = route === "tools";
  const books = route === "books";

  return (
    <div className="App">
      <NavBar
        route={route}
        onSelect={route => changeRoute(route)}
      />
      {home && <Homepage />}
      {lessonPlans && <TOC />}
      {books && <Books />}
      {tools && <Tools />}
      <Foot />
    </div>
  );
}

export default App;


